































































































































.tags-view-container {
  height: 36px;
  width: 100%;
  background: #fff;
  border-bottom: 1px solid #d8dce5;
  box-shadow: 0 1px 3px 0 rgba(99, 99, 99, .08), 0 0 3px 0 rgba(99, 99, 99, .04);
  white-space:nowrap;
  line-height: 36px;
  .tags-view-item {
    cursor: pointer;
    display: inline-block;
    margin-left: 8px;
  }
}
