


































































.root {
  position: fixed;
  bottom: 0;
  top: 0;
  background: #052644;
  z-index: 1002;
  .menu{
    transition: width 300ms;
    .logo{
      width: 100%;
      display: block;
      margin: 0 auto;
      padding: 18px;
      padding-bottom: 10px;
    }
    :global(.el-menu){
      border-right: none;
    }
  }

}
