























































.navbar {
  height: 50px;
  overflow: hidden;
  display: flex;
  background: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 10px;
  box-shadow: 0 1px 4px rgba(0,21,41,.08);
  .left-menu{
    display: flex;
    justify-content: flex-start;
    align-items: center;
  }
  .right-menu{
    display: flex;
    justify-content: flex-end;
    align-items: center;
    .screenfull{
      margin: 0 5px;
    }
    .badge{
      margin: 0 5px 0;
    }
    .problem {
      cursor: pointer;
      padding: 0 12px;
      color: #409EFF;
      font-size: 14px;
    }
  }
}
